import mysql from 'mysql'
import React from 'react';
import { VKMiniAppAPI } from '@vkontakte/vk-mini-apps-api';
import connect from '@vkontakte/vkui-connect';
import {  View, ScreenSpinner, Epic, Button,PanelHeader, Panel, Tabbar, TabbarItem, Avatar,Alert, Div, HeaderButton, Cell, Snackbar, Gallery, Group, Tabs, TabsItem} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Profile from './panels/Profile';
import Modal from './panels/Modal';
import Achievment from './panels/Achievment';
import MapContainer from './panels/MapContainer';
import End from './panels/End'
import Poll from './panels/Poll';
import Begin from './panels/Begin'
import Questions from './panels/Questions';
import Rr from './panels/Rr';

import Icon28MoreHorizontal from '@vkontakte/icons/dist/28/more_horizontal';
import Icon28Place from '@vkontakte/icons/dist/28/place';
import Icon28User from '@vkontakte/icons/dist/28/user';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon16Done from '@vkontakte/icons/dist/16/done';

var axios = require('axios');


const orangeBackground = {
  backgroundImage: 'linear-gradient(135deg, #ffb73d, #ffa000)'
};

const blueBackground = {
  backgroundColor: 'var(--accent)'
};
 const api = new VKMiniAppAPI();

class App extends React.Component {
  constructor (props) {
    super(props);
    this.num = 2;

    this.state = {
      activeStory: 'other',
      activePanelMap: "map",
      activeTab:'questions',
      header: true,
      popout: null,
      selectedPlace:null,
      fetchedUser: null,
      ball:0,
      access_token: null,
      geoData: null,
      marker:null,
      errorGeo:null,
      Poll:null,
      showEnd:false


    };
  }
    componentDidMount(e){
    api.initApp();
    api.getUserInfo().then(userInfo => {
    this.setState({fetchedUser:userInfo});
    axios.post("https://www.questsinpocket.com/vk/index.php?type=create&vk_id="+this.state.userInfo.id+"$ball=0").then(data=>{
      if(data.data === 2){
        axios.post("https://www.questsinpocket.com/vk/index.php?type=get&vk_id="+this.state.userInfo.id).then(data=>{
          this.setState({ball:data.data})
        })
      }
    })
    console.log(userInfo.id);
    })


    }

    onClickMap=(e)=>{
      axios.get("https://www.questsinpocket.com/vk/index.php?type=get_all_points")
      .then(data=>{this.setState({marker:data.data})});
      this.setState({popout:<ScreenSpinner />})
      api.getGeodata().then(dataGeo=>{
        this.setState({geoData: dataGeo, activeStory:'map', popout:null});
        console.log(dataGeo);
      });

    }

    updateGeodata=(e)=>{
      api.getGeodata().then(dataGeo=>{
      this.setState({geoData: dataGeo});
      console.log(dataGeo);
      })
    }

    getPoll(e){
      axios.get("https://www.questsinpocket.com/vk/index.php?type=get&vk_id=0&id="+this.state.selectedPlace.id_marker).then(data=>{
        this.setState({Poll: data.data});
      })

    }

    checkGeo(a, e){
      this.setState({popout: <ScreenSpinner />})
      this.updateGeodata();
      if((this.state.geoData.lat).toFixed(this.num) === (a.position.lat).toFixed(this.num) &&
        (this.state.geoData.long).toFixed(this.num) === (a.position.lng).toFixed(this.num)){
        axios.post("https://www.questsinpocket.com/vk/index.php?type=get&vk_id="+this.state.fetchedUser.id+"$ball=0")
        this.setState({selectedPlace:a, popout:null, activePanelMap: 'quest'});

      }else{this.setState({errorGeo:<p style={{"color":"red"}}>Попробуйте еще раз</p> , popout:null});
       }
    }

    openDefault (e) {
    this.setState({ popout:
      <Alert
        actions={[{
          title: 'Отмена',
          autoclose: true,
          style: 'cancel'
        }, {
          title: 'Ок',
          autoclose: true,
        }]}
        onClose={this.closePopout}
      >

        <p>Для стабильной работы включите геолокацию</p>
      </Alert>
    });
  }

  closePopout=(e)=> {this.setState({ popout: null})}

  onStoryChange =(e)=> {
    this.setState({ activeStory: e.currentTarget.dataset.story });
  }

  onToChange =(e)=> {
     this.setState({ activePanel: e.currentTarget.dataset.to});

  }

  render(){
    return (
      <Epic activeStory={this.state.activeStory} tabbar={
        <Tabbar>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'profile'}
            data-story="profile"
          ><Icon28User /></TabbarItem>
          <TabbarItem
            onClick={this.onClickMap}
            selected={this.state.activeStory === 'map'}
            data-story="map"

          ><Icon28Place/></TabbarItem>
          <TabbarItem
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'other'}
            data-story="other"
          ><Icon28MoreHorizontal /></TabbarItem>
        </Tabbar>}>

      <View id={'profile'} popout={this.state.popout} activePanel={'profile'}>
        <Panel id={"profile"}>
          <Profile fetchedUser={this.state.fetchedUser} />
        </Panel>
      </View>

      <View id={'map'} popout={this.state.popout} activePanel={this.state.activePanelMap}>
        <Panel id={'map'}>
          <MapContainer geoData={this.state.geoData} updateGeodata={this.updateGeodata} marker={this.state.marker}/>
        </Panel>
        <Panel id="quest">
          <Begin />
          <Poll />
          <End />
        </Panel>
      </View>

      <View id={'other'} header={false} popout={this.state.popout} activePanel={'other'}>
        <Panel id={'other'}>
          <Questions />
        </Panel>

      </View>
      </Epic>)
  }
}

export default App;
