import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, ScreenSpinner, Epic, PanelHeader, Panel, Tabbar, TabbarItem, Avatar, Div, HeaderButton, List, Cell, Snackbar, Button} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Polls from 'react-polls';

const StyleButton = {
  outline: "none",
  width:"100%",
  color: "white",
  fontFamily:"Arial",
  padding: "20px 30px",
  borderRadius: "10px",
  backgroundImage: "linear-gradient(to right, #9EEFE1 0%, #4830F0 51%, #9EEFE1 100%)",
  backgroundSize: "200%",
  boxShadow:" 0 0 20px rgba(0,0,0,.1)",
  transition: ".5s",
  marginTop: "1.5%"
}


class Poll extends React.Component {

  // Handling user vote
  // Increments the votes count of answer when the user votes




  render(){

    
   return(
    <Div>
     <Cell align="center"><p><strong>{this.props.selectedPlace.pollQuestions}</strong></p></Cell>
    <Button id="falseBut"  onClick={ this.props.getAns}  style={StyleButton}>{this.props.selectedPlace.answer_1}</Button>
    <Button id="trueBut" onClick={this.props.getAns}  style={StyleButton}>{this.props.selectedPlace.answer_2}</Button>
    <Button id="falseBut" onClick={this.props.getAns}  style={StyleButton}>{this.props.selectedPlace.answer_3}</Button>
    </Div>
	 	);
	}
}

export default Poll;